import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { GetOwnUser } from '@backbase/data-ang/user';

@Component({
  selector: 'bb-impersonation-banner-layout',
  templateUrl: 'banner-layout.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ImpersonationBannerLayoutComponent {
  /**
   * The details of the user who is being impersonated.
   */
  @Input() user: GetOwnUser | undefined;

  /**
   * Emits when the impersonation session stop button is clicked.
   */
  @Output() readonly stop = new EventEmitter<void>();
}
